<template>
    <div class="main">
        <div style="height:220px;over-flow:hidden">
			<div class="swiper-container" style="background-color:#ebebeb;padding-top:10px">
		    <div class="swiper-wrapper">
		        <div class="swiper-slide" v-for="schoolPhoto in schoolPhotos">
		        	<div class="rolling-photo">
		        		<img style="width:100%;height:100%" :src="schoolPhoto.rollURL"/>
		        	</div>		        	
		        </div>
		    </div>
		    <!-- 如果需要分页器 -->
		    <div class="swiper-pagination" style="margin-bottom:7px"></div>
		</div>
		</div>
		
		
		<div class="school-select-body" >
			<div class="select-left">
				<a :class="{ activeSelect: index === 0 }" @click="changClass(0)" href="#">
					<span class="select-left-text" :style="borderBottom">同城院校</span>
				</a>
			</div>
			<div class="select-right">
				<a :class="{ activeSelect: index === 1 }" @click="changClass(1)" href="#">
					<span class="select-right-text">热门院校</span>
				</a>
			</div>
		</div>
		<div class="swiper-container2">
		    <div class="swiper-wrapper">

		        <div class="swiper-slide" v-for="result in results">
		        	<div class="rolling-card">
				        <div class="rolling-lg-card"> 
				            <a href="#">
				                <!-- logo -->
				                <div class="rolling-clg-logo" >
				                    <img :src="result.logoURL" style="width: 100%;" alt=""/>
				                </div>
				                <div class="rolling-cname">
				                    <p>{{result.cname}}</p>
				                </div>
				                <div class="rolling-ename">
				                    <p>{{result.ename}}</p>
				                </div>
				            </a>
				        </div>
		        	</div>
		        </div>
			</div>
    	</div>
    </div>
</template>
<script>
import swiper from '../assets/swiper/swiper.min.js'
import CompassHeader from '../components/CompassHeader'

export default {
	props: {
		results: {
			type: Array,
			default() {
		       return [
		       		{logoURL:require('assets/images/ap1.png'),	
		       		 cname: '悉尼大学',
		       		 ename: 'The Universitu Sydney'},
					{logoURL:require('assets/images/ap1.png'),
		       		 cname: '悉尼大学',
		       		 ename: 'The Universitu Sydney'},
					{logoURL:require('assets/images/ap1.png'),
		       		 cname: '悉尼大学',
		       		 ename: 'The Universitu Sydney'},
		       ]
		    }
		},
		schoolPhotos:{
			type: Array,
			default() {
		       return [
			       {rollURL:require('../assets/images/p7.png')},
			       {rollURL:require('../assets/images/p7.png')},
			       {rollURL:require('../assets/images/p7.png')},	       
		       ]
		   }
		}
	},
    data() {
        return {
        	index: 0,
        }
    },
    methods: {
    	changClass(id){
          this.index = id;
      	}
    },
    ready: function(){
	        var mySwiper1 = new Swiper ('.swiper-container', {
		    direction: 'horizontal',
		    loop: true,
		    autoplay: 3000,
		    pagination: '.swiper-pagination',
	  		})
	  		var mySwiper2 = new Swiper ('.swiper-container2', {
		    freeMode : true,
		    loop: true,
		    slidesPerView: 2.05,
	        paginationClickable: true,
	        spaceBetween: 50
	  		})
    },
    components: {
       CompassHeader,
    },
        
     	
}
</script>
<style>
@import '../assets/swiper/swiper.min.css';

.rolling-card{
	/*margin-top: 10px;*/
	overflow: hidden;
	width: 1.725rem;
	height: 1.25rem;
	/*line-height:125px;*/
	background-color:#ffffff;
	/*vertical-align: middle;*/
	/*border: 1px solid red;*/
}
.rolling-lg-card{
	width: 1.525rem;
	height: 1.25rem;
	margin:0 auto;
}
.rolling-clg-logo{
	height:.52rem;
	margin-top: .10rem;
}
.rolling-cname{
	border-top: .01rem solid #ebebeb;
	padding-top: .02rem;
	margin-top: .15rem;
}
.rolling-ename{
	margin-top: .05rem;
}
.swiper-container2{
	overflow: hidden;
	height:1.45rem;
	padding-top: .10rem;
	background-color: #0d2f60;	
}
.swiper-container {
    width: 100%;
    height: 2.20rem;
    overflow: hidden;
}
.select{
	width: 100%;
	height:.50rem;
}
.select-left{
	margin-top: .15rem;
	margin-left: .60rem;
	float: left;
	font-size: .13rem;
	font-weight:bold;
}
/*.select-left-text{
	padding-bottom: .07rem;
	border-bottom: .02rem solid #0d2f60;
}*/
.select-right{
	margin-top: .15rem;
	margin-right: .60rem;
	float: right;
	font-size: .13rem;
	font-weight:bold;
}
/*.select-right-text{
	padding-bottom: .07rem;
	border-bottom: .02rem solid #0d2f60;
}*/
.activeSelect{
  	padding-bottom: .07rem;
	border-bottom: .02rem solid #0d2f60;
}
.rolling-photo{
	overflow: hidden;
	/*margin-top: 10px;*/
	height: 2.00rem;
	background-color: #ffffff;
}
.school-select-body{
	width: 100%;
	height:.50rem;
}
.select-left{
	margin-top: .18rem;
	margin-left: .60rem;
	float: left;
	font-size: .15rem;
	font-weight:bold;
}
.select-right{
	margin-top: .18rem;
	margin-right: .60rem;
	float: right;
	font-size: .15rem;
	font-weight:bold;
}
</style>



