<template>

  <school-news v-if="page=='SchoolNews'"></school-news>

  <person :data="data" v-if="page=='Person'"></person>
  <Login v-if="page=='Login'"></Login>
  <Signup v-if="page=='Signup'"></Signup>
  <Home :data="data" v-if="page=='Home'"></Home>
  <Evaluate v-if="page=='Evaluate'"></Evaluate>
  <School-News v-if="page=='SchoolNews'"></School-News>
  <Picture v-if="page=='Picture'"></Picture>
  <Major v-if="page=='Major'"></Major>
  <Admission v-if="page=='Admission'"></Admission>
  <School-Home v-if="page=='SchoolHome'"></School-Home>
  <Rank-List v-if="page=='RankList'"></Rank-List>
  <Our-School-Rank v-if="page=='OurSchoolRank'"></Our-School-Rank>
  <School-Rank v-if="page=='SchoolRank'"></School-Rank>
  <School-Type v-if="page=='SchoolType'"></School-Type>
  <Select-Major v-if="page=='SelectMajor'"></Select-Major>
  <Select-Area v-if="page=='SelectArea'"></Select-Area>
  <College-Screen v-if="page=='CollegeScreen'"></College-Screen>
  <College-Query :data="data" v-if="page=='CollegeQuery'"></College-Query>
  <School-News v-if="page=='SchoolNews'"></School-News>

  <Evaluate-Result v-if="page=='EvaluateResult'"></Evaluate-Result>


</template>

<script>
  import EvaluateResult from './pages/EvaluateResult'
  import CollegeQuery from './pages/CollegeQuery'
  import CollegeScreen from './pages/CollegeScreen'
  import SelectArea from './pages/SelectArea'
  import SelectMajor from './pages/SelectMajor'
  import SchoolType from './pages/SchoolType'
  import SchoolRank from './pages/SchoolRank'
  import OurSchoolRank from './pages/OurSchoolRank'
  import RankList from './pages/RankList'
  import SchoolHome from './pages/SchoolHome'
  import Admission from './pages/Admission'
  import Major from './pages/Major'
  import Picture from './pages/Picture'
  import SchoolNews from './pages/SchoolNews'
  import ListView from './components/ListView'
  import Evaluate from './pages/Evaluate'

  import Person from './pages/Person'
  import Login from './pages/Login'
  import Signup from './pages/Signup'
  import Home from './pages/Home'

  export default {
    components: {
      EvaluateResult,
      CollegeQuery, CollegeScreen, SelectArea,
      SelectMajor, SchoolType, SchoolRank, OurSchoolRank, Picture,
      RankList, SchoolHome, Admission, Major, Evaluate, Person, Login, Signup, Home, SchoolNews
    },
    props: {
      data: {
        type: Object,
        default(){
          return global.data;
        }
      },
      page: {
        type: String,
        required: true,
        default(){
          return 'Login'
        }
      }

    }

  }
</script>

<style lang="stylus">
  body {
    position: relative;
    margin: 0;
    background: #ebebeb;
  }

  html {
    font-size: 100px;
  }

  * {
    margin: 0;
    padding: 0;
    list-style: none;
    outline: none;
    font-family: "Helvetica Neue", Arial, verdana, sans-serif;
  }

  /*
  KISSY CSS Reset
  理念：1. reset 的目的不是清除浏览器的默认样式，这仅是部分工作。清除和重置是紧密不可分的。
  2. reset 的目的不是让默认样式在所有浏览器下一致，而是减少默认样式有可能带来的问题。
  3. reset 期望提供一套普适通用的基础样式。但没有银弹，推荐根据具体需求，裁剪和修改后再使用。
  特色：1. 适应中文；2. 基于最新主流浏览器。
   */

  /** 清除内外边距 **/
  body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, /* structural elements 结构元素 */
  dl, dt, dd, ul, ol, li, /* list elements 列表元素 */
  pre, /* text formatting elements 文本格式元素 */
  form, fieldset, legend, button, input, textarea, /* form elements 表单元素 */
  th, td /* table elements 表格元素 */
  {
    margin: 0;
    padding: 0;
  }

  /** 设置默认字体 **/
  body,
  button, input, select, textarea /* for ie */
  {
    font: 12px / 1.5 tahoma, arial, \5b8b\4f53, sans-serif;
  }

  h1, h2, h3, h4, h5, h6 {
    font-size: 100%;
  }

  address, cite, dfn, em, var {
    font-style: normal;
  }

  /* 将斜体扶正 */
  code, kbd, pre, samp {
    font-family: courier new, courier, monospace;
  }

  /* 统一等宽字体 */
  small {
    font-size: 12px;
  }

  /* 小于 12px 的中文很难阅读，让 small 正常化 */

  /** 重置列表元素 **/
  ul, ol {
    list-style: none;
  }

  /** 重置文本格式元素 **/
  a {
    text-decoration: none;
    /** 去除阴影*/
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    &:hover, &:active {
      text-decoration: none;
    }
  }

  /** 重置表单元素 **/
  legend {
    color: #000;
  }

  /* for ie6 */
  fieldset, img {
    border: 0;
  }

  /* img 搭车：让链接里的 img 无边框 */
  button, input, select, textarea {
    font-size: 100%;
  }

  /* 使得表单元素在 ie 下能继承字体大小 */
  /* 注：optgroup 无法扶正 */

  /** 重置表格元素 **/
  table {
    border-collapse: collapse;
    border-spacing: 0;
  }

  .container {
    margin: 0 auto;
    max-width: 3rem;
  }

  .container-fluid {
    margin: 0 auto;
    width: 90%;
  }

  .btn-clear {
    padding: 0;
    border: none;
    background: none;
    color: #fff;
  }

  html {
    box-sizing: border-box;
  }

  *, *:before, *:after {
    box-sizing: inherit;
  }

  a {
    color: #2D2D2D;
    text-decoration: none;
  }


</style>



