<template>
  <div class="main">
    <div class="head-top">
      <compass-header title="学校类型"></compass-header>
    </div>
    <div class="m-container">
      <ul>
        <li id="li-0" @click=" changeClass(0) ">不限</li>
        <li v-for=" type in typeList " id="li-{{ $index+1 }}" :class=" { selected : isSelected } "
            @click=" changeClass($index+1) ">{{ type.name }}
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
  import CompassHeader from '../components/CompassHeader'
  import LoginForm from '../components/LoginForm'

  export default {
    data() {
      return {
        typeList: [{
          name: '八大',
          isSelected: false
        }, {
          name: '非八大',
          isSelected: false
        }]
      }
    },
    methods: {
      changeClass(index) {
        let node = document.getElementById('li-' + index);
        // console.log(node)
        node.style.backgroundColor = '#0D2F60';

        // console.log(type.isSelected);
        // type.isSelected = !type.isSelected;
      },
    },
    components: {
      CompassHeader,
      LoginForm,
    },

  }
</script>
<style scoped>
  .m-container {
    width: 100%;
  }

  .m-container ul li {
    height: .51rem;
    width: 100%;
    padding-left: 2%;
    line-height: .51rem;
    border-bottom: 1px solid #ebebeb;
  }

  .selected {
    background-color: #0D2F60;
  }
</style>



