<template>
  <div class="main">
    <div class="top-img-wrap">
      <img id="title_img" :src="collage.schoolShowUrl" />
    </div>
    <div class="sch-info item-body">
      <img id="badge_img" :src="data.logoUrl">
      <div id="name_body" style="float:right">
        <div id="name_text"><span style="float:right">{{ data.cname }}</span></div>
        <div id="name_English"><span style="float:right">{{data.ename }}</span></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  components: {},
  props: {
   
    data: {
      type: Object,
      default: function() {
        return {
          msg: 'hello',
          // cname: '悉尼大学',
          // ename: 'The University of Sydney',
          // logoUrl: '../assets/images/p1.png',
        }
      }
    },
  }
}
</script>
<style>
.title_body {
  width: 100%;
}

.sch-info {
  margin-bottom: .3rem;
}

#title_img {
  width: 100%;
}

.schoolName {
  width: 100%;
}

#badge_img {
  height: 37px;
  margin-top: 25px;
}

#name-body {
  float: right;
}

#name_text {
  color: #000001;
  font-weight: bold;
  font-size: 15px;
  margin-top: 23px;
  margin-right: 10px;
}

#name_English {
  font-size: 14px;
  color: #838383;
  margin-right: 10px;
  margin-top: 50px;
}

.top-img-wrap {
  overflow: hidden;
  height: 4.45rem;
}
</style>



